.menu {
    padding: 20px 180px;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: flex-start;
}

.foodGallery .foodWrapper {
    background-color: blue;
    height: auto;
    width: auto;
    display: flex;
    flex-direction: column;
    margin: auto;
    align-items: center;
    justify-content: space-evenly;
    padding: 5px;
}

.foodGallery .foodWrapper .foodImageWrapper {
    height: 200px;
    width: auto;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.foodGallery .foodWrapper .foodImageWrapper img {
    height: inherit;
    width: auto;
    margin: auto;
    display: flex;
    object-fit: contain;
    border-radius: 20px;
}
.foodNameAndLikes{
    display: flex;
    width: 100%;
    justify-content: space-between;
    align-items: center;
}
.rating{
    width: 100%;
    display: flex;
}