@import url('https://fonts.googleapis.com/css2?family=Just+Me+Again+Down+Here&family=Kaushan+Script&family=Lato&display=swap');

* {
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
}

.sectionHeader {
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-around;
}

.sectionHeader .headerTag1 {
    font-family: lato;
    color: #E61A32;
    display: flex;
    display: flex;
    font-size: 80px;
    font-weight: bold;
    -webkit-text-stroke-width: 1px;
    -webkit-text-fill-color: transparent;
}

.sectionHeader .headerTag2 {
    display: flex;
    font-family: lato;
    display: flex;
    font-size: 30px;
    font-weight: bold;
    color: #E61A32;
}